html {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  background-color: #eeeded;
}

.navigation {
  margin-top: 150px;
  border-bottom: 1px solid #afacac;
}

.navigation ul {
  display: flex;
  list-style: none;
  justify-content: center;
  padding: 0;
  margin-bottom: 0;
}

.navigation li {
  text-align: center;
  margin: 0;
  padding: 15px;
}

.navigation li:hover {
  background-color: #afacac;
  cursor: pointer;
}

.selected {
  background-color: #afacac;
}

.contents {
  text-align: center;
}

.contents h2 {
  font-size: 40px;
}

.contents {
  margin-top: 100px;
  font-size: 55px;
}

.contents .defList {
  padding-left: 40%;
  padding-right: 25%;
}

.contents dl {
  font-size: 20px;
  text-align: left;
}

.contents dl ul {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
}

.contents dl p {
  margin-top: 0;
  margin-bottom: 0;
}

.contents .main .name {
  margin-bottom: 10px;
}

.contents .main .sub {
  font-size: 20px;
}